@import "https://fonts.googleapis.com/css2?family=Heebo:wght@400;700&display=swap";
* {
  margin: 0;
  padding: 0;
}

body {
  height: 100vh;
  background-color: #000;
  justify-content: center;
  align-items: center;
  font-family: Heebo, sans-serif;
  display: flex;
  overflow: hidden;
}

button {
  color: #fff;
  text-transform: uppercase;
  letter-spacing: 1.5px;
  cursor: pointer;
  background-color: purple;
  border: none;
  border-radius: 30px;
  margin: 10px 0;
  padding: 15px 25px;
  font-weight: 700;
  position: relative;
  overflow: hidden;
}

button:hover {
  border: 2px solid purple;
  outline: none;
}

button .circle {
  width: 100px;
  height: 100px;
  background-color: #fff;
  border-radius: 50%;
  animation: scale .75s ease-out;
  position: absolute;
  transform: translate(-50%, -50%)scale(0);
}

button .dark-circle {
  width: 100px;
  height: 100px;
  background-color: #000;
  border-radius: 50%;
  animation: scale .75s ease-out;
  position: absolute;
  transform: translate(-50%, -50%)scale(0);
}

@keyframes scale {
  to {
    opacity: 0;
    transform: translate(-50%, -50%)scale(3);
  }
}

